'use strict';

WUI.ready = function () {
  var $iframe = $('iframe');
  var queryString = window.location.search;
  var innerUrl = queryString.slice('?innerUrl='.length);

  $iframe
    .height($('.content-wrapper').height())
    .attr('src', innerUrl);
};

$(function () {
  WUI.init({
    system: 'order'
  });
});

